// src/features/createReviewEvent/EventPoaster/EventPoaster.js
import React, { useEffect, useRef, useState } from "react";
import "./EventPoaster.css"; // :   

/**
 *   ( )
 * -    X
 * - ""     
 *
 * Props:
 *  - onChange?: (file: File | null) => void
 *  - maxSizeMB?: number ( 5MB)
 */
export default function EventPoaster({ onChange, maxSizeMB = 5 }) {
  const fileInputRef = useRef(null);
  const [file, setFile] = useState(null);
  const [previewURL, setPreviewURL] = useState("");
  const [open, setOpen] = useState(false);

  const handleFileChange = (e) => {
    const f = e.target.files?.[0];
    if (!f) return;

    //  /  
    if (!f.type.startsWith("image/")) {
      alert("    .");
      fileInputRef.current && (fileInputRef.current.value = "");
      setFile(null);
      onChange?.(null);
      return;
    }
    const maxBytes = maxSizeMB * 1024 * 1024;
    if (f.size > maxBytes) {
      alert(`  ${maxSizeMB}MB  .`);
      fileInputRef.current && (fileInputRef.current.value = "");
      setFile(null);
      onChange?.(null);
      return;
    }

    setFile(f);
    onChange?.(f);
  };

  // Blob URL /
  useEffect(() => {
    if (!file) {
      setPreviewURL("");
      return;
    }
    const url = URL.createObjectURL(file);
    setPreviewURL(url);
    return () => URL.revokeObjectURL(url);
  }, [file]);

  // ESC  
  useEffect(() => {
    if (!open) return;
    const onKeyDown = (e) => e.key === "Escape" && setOpen(false);
    window.addEventListener("keydown", onKeyDown);
    return () => window.removeEventListener("keydown", onKeyDown);
  }, [open]);

  const clearFile = () => {
    setFile(null);
    setPreviewURL("");
    if (fileInputRef.current) fileInputRef.current.value = "";
    onChange?.(null);
  };

  return (
    <div className="poster-upload">
      {/*   */}
      <label className="btn poster-btn" title="  ">
         
        <input
          ref={fileInputRef}
          type="file"
          accept="image/*"
          onChange={handleFileChange}
          style={{ display: "none" }}
        />
      </label>

      {/*  /  */}
      <button
        type="button"
        className="btn poster-btn"
        onClick={() => setOpen(true)}
        disabled={!file}
        title={file ? "" : "  "}
      >
        
      </button>
      <button
        type="button"
        className="btn poster-btn"
        onClick={clearFile}
        disabled={!file}
        title={file ? "  " : "  "}
      >
        
      </button>

      {/*  () */}
      {file && (
        <div className="poster-filename" title={file.name}>
          {file.name}
        </div>
      )}

      {/*  (  ) */}
      {open && (
        <div
          className="poster-modal-overlay"
          onClick={(e) => {
            if (e.target.classList.contains("poster-modal-overlay")) setOpen(false);
          }}
        >
          <div className="poster-modal" role="dialog" aria-modal="true" aria-label=" ">
            <div className="poster-modal-header">
              <strong> </strong>
              <button type="button" className="btn small" onClick={() => setOpen(false)}>
                
              </button>
            </div>
            <div className="poster-modal-body">
              {previewURL ? (
                <img src={previewURL} alt="  " className="poster-preview-img" />
              ) : (
                <p>  .</p>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
